@font-face {
  font-family: "Factor A";
  src: url("./FactorA/FactorA-Regular.eot");
  src: local("Factor A Regular"), local("FactorA-Regular"),
    url("./FactorA/FactorA-Regular.eot") format("embedded-opentype"),
    url("./FactorA/FactorA-Regular.woff2") format("woff2"),
    url("./FactorA/FactorA-Regular.woff") format("woff"),
    url("./FactorA/FactorA-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

